<template>
  <div class="complaint">
    <div
      class="complaint_left charts_main"
      v-loading="!restsywStatus"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">投诉业务一级分类占比</div>
      <div class="pie_charts">
        <bda-pie-charts
          v-if="restsywStatus"
          :pieChartsData="tsywPieData"
          :roseType="false"
          :radius="['45%', '75%']"
          :center="['29%', 'center']"
          :labelType="'emphasis'"
          :legendShow="true"
        ></bda-pie-charts>
      </div>
    </div>
    <div class="complaint_center">
      <div
        class="complaint_center_pie charts_main"
        v-loading="!resscyxStatus"
        element-loading-background="rgba(0, 0, 0, 0.2)"
        element-loading-text="加载中"
      >
        <div class="title">生产营销投诉占比类别</div>

        <div class="pie_charts">
          <bda-pie-charts
            v-if="resscyxStatus"
            :pieChartsData="scyxPieData"
            :roseType="false"
            :radius="['0%', '80%']"
            :center="['15%', 'center']"
            :labelType="'none'"
            :legendShow="true"
            :legendRight="70"
            :legendTop="10"
          ></bda-pie-charts>
        </div>
      </div>
      <div
        class="complaint_center_count"
        v-loading="!kbStatus"
        element-loading-background="rgba(0, 0, 0, 0.2)"
        element-loading-text="加载中"
      >
        <div v-for="(v, k, i) in kbMap" :key="i" class="count_item">
          <div class="count_title">{{ k }}</div>
          <div class="count_value">{{ parseInt(v) }}</div>
        </div>
      </div>
    </div>
    <div
      class="complaint_right charts_main"
      v-loading="!dwChartsStatus"
      v-if="dwChartsStatus"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">各供电单位投诉总量/属实量</div>
      <div class="bar_charts">
        <bda-bar-charts
          v-if="dwChartsStatus"
          :chartsConfig="dwChartsData"
          :barType="barType"
          :barWidth="6"
        ></bda-bar-charts>
      </div>
    </div>

    <div
      class="complaint_right charts_main"
      v-loading="!tsChartsStatus"
      v-if="tsChartsStatus"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">各供电单位投诉总量/属实量</div>
      <div class="bar_charts">
        <bda-bar-charts
          v-if="tsChartsStatus"
          :chartsConfig="tsChartsData"
          :barType="barType"
          :barWidth="4"
        ></bda-bar-charts>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '../../../base/mixin/service-quality-process';
import qs from 'qs';
import { dataChange, dataFilter, translateFilter } from '../../../commonJS/dataChanges'
import bdaBarCharts from '../../../widgets/bda-3d-barCharts/bda-3d-barCharts';
import bdaPieCharts from '../../../widgets/bda-pie-charts/bda-pie-charts'

export default {
  name: "complaint",
  components: { bdaBarCharts, bdaPieCharts, },
  mixins: [Interface],
  data() {
    return {
      kbMap: {
        '运检投诉总量': 0,
        '运检投诉属实量': 0,
        '营销服务投诉总量': 0,
        '营销服务投诉属实量': 0
      },
      kbStatus: false,
      dwChartsStatus: false,
      dwChartsData: {},
      barType: 'stack',
      tsChartsData: {},
      tsChartsStatus: false,
      tsywPieData: {
        dataMap: [],
        color: ['#2CFAFF', '#00CDFF', '#00ABFF', '#0091FF', '#0079FF', '#005FFF', '#4D7EFF', '#8655ff']
      },
      restsywStatus: false,
      scyxPieData: {
        dataMap: [],
        color: ['#2CFAFF', '#00CDFF', '#00ABFF', '#0091FF', '#0079FF', '#005FFF', '#4D7EFF', '#8655ff']
      },
      resscyxStatus: false
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    // this.getTsData('0');
    this.getData()
  },
  methods: {
    getPieData() {
      let $this = this;
      this.restsywStatus = false;
      let ismarketing = {
        '0': '运检投诉',
        '1': '营销服务投诉'
      };
      let dataFilter = (data, type) => {
        let infoData = [];
        if (data) {
          for (let item of data) {
            let _obj = {}
            if (type === '1') {
              _obj = {
                name: item.name,
                value: item.ccsum
              }
            } else {
              _obj = {
                name: ismarketing[item.ismarketing] ? ismarketing[item.ismarketing] : '',
                value: item.sum
              }
            }

            infoData.push(_obj)
          }
        }
        return infoData;
      };
      //投诉业务一级分类占比
      this.getComplainForType(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams,
        orgType: this.$parent.orgType
      })).then(res => {
        let _dataInfo = dataFilter(res, '1');
        $this.tsywPieData.dataMap = _dataInfo;
        $this.restsywStatus = false;
        $this.$nextTick(() => {
          $this.restsywStatus = true;
        })
      }).catch(() => {
        $this.restsywStatus = true
      });
      //生产营销投诉占比类别
      this.getComplainForMarketin(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams,
        orgType: this.$parent.orgType
      })).then(res => {
        let _dataInfo = dataFilter(res, '2');
        $this.scyxPieData.dataMap = _dataInfo;
        $this.resscyxStatus = false;
        $this.$nextTick(() => {
          $this.resscyxStatus = true;
        })
      }).catch(() => {
        $this.resscyxStatus = true;
      });
    },
    //各供电单位投诉总量/属实量
    getTsData() {
      let $this = this;
      let type = this.$parent.orgType
      this.getComplainForVerified(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams,
        orgType: type
      })).then(res => {
        if (res && res.length !== 0) {
          let data = translateFilter(res, $this.$parent.orgTranslate, 'orgno', 'orgName');
          let _info = dataChange(data, 'orgno', 'orgName');
          let colorMap = {
            '属实': ['#6AFFA2', '#00BDDE'],
            '不属实': ['#005FFF', '#009DFF'],
          }
          let nData = dataFilter(_info, {
            name: 'istrue',
            translate: {
              '0': '不属实',
              '1': '属实'
            },
            valueField: 'sum'
          }, 'orgName');
          nData.colorMap = colorMap;
          if (type === '1') {
            $this.tsChartsStatus = false;
            $this.dwChartsData = nData;
            $this.$nextTick(() => {
              $this.dwChartsStatus = true;
            })
          } else {
            $this.dwChartsStatus = false;
            $this.tsChartsData = nData;
            $this.$nextTick(() => {
              $this.tsChartsStatus = true;
            })
          }

        } else {
          if (type === '1') {
            $this.dwChartsStatus = true;
          } else {
            $this.tsChartsStatus = true;
          }
        }
      })
        .catch(() => {
          if (type === '1') {
            $this.dwChartsStatus = true;
          } else {
            $this.tsChartsStatus = true;
          }
        })
    },

    //总投诉量
    getTotalComplain() {
      let $this = this;
      this.getComplainTarget(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams,
        orgType: this.$parent.orgType
      })).then((res) => {
        this.kbStatus = true;
        if (res && res.length !== 0) {
          let fz = dataChange(res, 'ismarketing');
          $this.kbMap['运检投诉总量'] = 0
          $this.kbMap['营销服务投诉总量'] = 0
          if (fz) {
            for (let key in fz) {
              if (key === '0' && fz[key].data) {
                for (let sys of fz[key].data) {
                  $this.kbMap['运检投诉总量'] += sys.sum;
                  if (sys.istrue && sys.istrue === '1') {
                    $this.kbMap['运检投诉属实量'] = sys.sum;
                  }
                }
              }
              if (key === '1' && fz[key].data) {
                for (let sys of fz[key].data) {
                  $this.kbMap['营销服务投诉总量'] += sys.sum;
                  if (sys.istrue && sys.istrue === '1') {
                    $this.kbMap['营销服务投诉属实量'] = sys.sum;
                  }
                }
              }
            }
          }
        }
      }).catch(() => {
        this.kbStatus = true;
      });
    },
    getData() {
      this.getTsData();
      this.getPieData();
      this.getTotalComplain();
    }
  }
}
</script>

<style scoped lang="scss">
.complaint {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  .noData {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      color: #fff;
      font-size: 14px;
    }
  }

  .title {
    width: 100%;
    height: 16%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-size: 20px;
    color: #00e7ff;
  }

  .charts_main {
    padding: 0 20px;
    box-sizing: border-box;
  }

  .complaint_left {
    width: calc(22%);
    background: url("../../../assets/img/监控框4.png") no-repeat top center;
    background-size: 100% 100%;
    height: 100%;
    float: left;
    position: relative;
    .pie_charts {
      width: 100%;
      height: calc(100% - 16%);
    }
  }
  .complaint_center {
    width: calc(22%);
    // background: url("../../../assets/img/监控框4.png") no-repeat top center;
    // background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .title {
      width: 100%;
      height: 24%;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      font-size: 20px;
      color: #00e7ff;
    }
    .pie_charts {
      width: 100%;
      height: calc(100% - 30%);
    }
    .complaint_center_pie {
      width: 100%;
      height: 55%;
      background: url("../../../assets/img/监控框5.png") no-repeat top center;
      background-size: 100% 100%;
    }
    .complaint_center_count {
      width: 100%;
      height: 45%;
      background: url("../../../assets/img/监控框6-1.png") no-repeat top center;
      background-size: 100% 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;

      box-sizing: border-box;
      padding: 20px 10px 10px 10px;
      .count_item {
        width: 50%;
        height: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 16px;
        // padding: 5px 5px 5px 5px;
        .count_title {
          color: #00e7ff;
        }
        .count_value {
          color: #fff;
          margin-top: 3px;
        }
      }
    }
  }

  .complaint_right {
    width: 56%;
    float: left;
    height: 100%;
    position: relative;
    background: url("../../../assets/img/监控框7.png") no-repeat top center;
    background-size: 100% 100%;

    .bar_charts {
      width: 100%;
      height: calc(100% - 16%);
    }
  }
}
</style>